/* src/components/Navbar.css */
.navbar {
    background-color: #1F1F1F; /* Koyu menü arka plan */
    padding: 10px 20px; /* Padding */
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5); /* Hafif gölge */
  }
  
  .navbar-list {
    list-style-type: none; /* Liste işaretlerini kaldır */
    display: flex; /* Yatay düzen */
    justify-content: space-around; /* Elemanlar arası boşluk */
    margin: 0;
    padding: 0;
  }
  
  .navbar-list li a {
    padding: 10px 15px; /* Buton görünümü için padding */
    transition: background-color 0.3s; /* Hover geçişi */
  }
  
  .navbar-list li a:hover {
    background-color: rgba(255, 215, 0, 0.2); /* Altın rengi hover */
  }
  